 @font-face {
  	font-family: Mainfont; /* Имя шрифта */
   	src: url(/static/media/font3.c33d3469.otf); /* Путь к файлу со шрифтом */
 }
 @font-face {
  	font-family: Discfont; /* Имя шрифта */
   	src: url(/static/media/font1.66d392d1.ttf); /* Путь к файлу со шрифтом */
 }
 p,
 h1,
 h2,
 h3,
 h4,
 h5 {
 	font-family:  Mainfont;
 	color: #000;
 }

.discription-text{
	font-family:  Discfont;
	font-size: 1.3em;
 	color: #000;
}


html,
body,
#root{
	height: 100%;
	width: 100%;
}



.background{
	position: relative;
	overflow: hidden;
}

.background:before{
	content: "";
	position: absolute;
	width: 500%;
	height: 500%;
	top: -200%;
	left: -200%;
	z-index: -1;
	background: url('/bg3.jpg') 50% 50% repeat;
	-webkit-animation: rotate 120.0s infinite linear;
	        animation: rotate 120.0s infinite linear;
}


.tools{
	height: 100%;
	position: relative;
	display: flex;
	z-index: -1;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background: rgba(255,0,0,0.4)
}

@-webkit-keyframes rotate{
	100%{
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg)
	}
}

@keyframes rotate{
	100%{
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg)
	}
}

canvas{
	position: absolute;
	z-index:0;
}

*{
	position: relative;
	z-index: 2;
}
.chat{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.input {
	align-self: flex-end;
	display: flex;
	justify-content: center;
	width: 10%;
}

.icon{
	display: flex;
	flex:1 1;
	background: #333;
	margin:2px;
	border-bottom-left-radius: 30px;
	border-top-left-radius: 10px;
	border-bottom:4px solid #000;
}

.icon > p {
	color:#FFF;
}
.message{
	flex:5 1;
	margin:3px;
	display: flex;
	background: rgba(255,255,255,0.7);
}
.textarea{
	display: grid;
	height: 150px;
	overflow: auto;
}
.textarea::-webkit-scrollbar{
	display: none;
}
.statistics{
	position: relative;
	display: flex;
	list-style: none;
	padding: 0;
	display: flex;
	justify-content: space-around;
	width: 100%;
	background: rgba(255,255,255,0.4);
}


.statistic-item{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
}

.statistics > li{
	position: relative;
	width:100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
	text-align: center;
}

.statistics img{
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}

.statistics img:hover{
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	cursor: pointer;
}

.statistics p{
	color: #000;
	font-size: 2.0em;
}

.modal-layer{
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	width:50vh;
	height:50vh;
	left:50%;
	top:50%;
	border-radius: 10px;
	background: rgba(255,255,255,0.5);
	-webkit-transform: translate(-50%,-50%);
	        transform: translate(-50%,-50%);
	z-index: 1000;
}

.modal-cross{
	position: fixed;
	right:0;
	top:0;
	cursor: pointer;
	color:#050;
	margin-top: 10px;
	margin-right: 10px;
}

.inner-modal-layer{
	position:relative;
}
.blur-container{
	width:100vw;
	height:100vh;
	position:relative;
	overflow: hidden;
	-webkit-transition: all 0.4s linear;
	transition: all 0.4s linear;
}

.my_message{
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width:200px;
	height:70px;
	border-top-right-radius: 20px;
	border-bottom-right-radius: 100px;
	border-top-left-radius: 60px;
	border:2px solid #eee;
	background: rgba(255,255,255,0.5);
	cursor: pointer;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.my_message-bottom{
	-webkit-transform: translate(0,50px);
	        transform: translate(0,50px);
	border-radius: 30px;
	width:20vw;
}


.my_message-top{
	-webkit-transform: translate(100px,-50px);
	        transform: translate(100px,-50px);
}


.class-selector{
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.class-selector__classes{
	display: flex;
	justify-content: center;
}
.class-selector__description{
	margin: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.heroes{
	margin: 10px;
	background: transparent;
	border: none;
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

.heroes:hover{
	 -webkit-transform: scale(0.8);
	         transform: scale(0.8);
	 cursor: pointer;
}

.heroes_selected{
	margin:20px;
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
	outline: 0;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
	cursor: pointer;
	border-radius: 50%;
	background: #636;
	border: 4px solid #000;
}

.game-bar_container * {
	margin-bottom: 8px;
}
.game-bar_container{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}

.game-bar{
	width:20px;
	border-radius: 10px;
	border:3px solid #333;
	position: absolute;
	left:0;
	bottom:0;
}

.game-bar-hero{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
}

@-webkit-keyframes rotate{
	0%{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	25%{
		-webkit-transform: rotate(30deg) scale(1.2);
		        transform: rotate(30deg) scale(1.2);
	}
	75%{
		-webkit-transform: rotate(-30deg) scale(1.2);
		        transform: rotate(-30deg) scale(1.2);
	}
	100%{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

}

@keyframes rotate{
	0%{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	25%{
		-webkit-transform: rotate(30deg) scale(1.2);
		        transform: rotate(30deg) scale(1.2);
	}
	75%{
		-webkit-transform: rotate(-30deg) scale(1.2);
		        transform: rotate(-30deg) scale(1.2);
	}
	100%{
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

}


.game-bar_container img:hover{
	-webkit-animation: rotate 1.5s infinite ease;
	        animation: rotate 1.5s infinite ease;
	cursor: pointer;
}



